.aboutHeader {
  /* background-image: url("../../images/headerImage.jpg"); */
  background-color: #f5f5dc;
  background-repeat: no-repeat;
  background-attachment: fixed;
  background-size: cover;
  background-position: center center;
  text-align: center;
  padding-top: 50px;
  padding-bottom: 50px;
  color: black;
}

.aboutMain {
  overflow: auto;
}
.aboutMainLeft {
  float: none;
  width: 48%;
  margin-bottom: 50px;
  text-align: center;
  margin-left: 1%;
  margin-right: 1%;
  height: 0;
}

.aboutMainLeft img {
  width: 100%;
  height: 100%;
  padding: 0;
  margin: 0;
  display: block;
}

.aboutMainLeftCard {
  margin: 0;
  padding: 0rem;
  height: 100%;
}

.aboutMainRight {
  float: none;
  width: 80%;
  margin-bottom: 50px;
  /* text-align: center; */
  margin-left: 10%;
  margin-right: 10%;
}

@media (min-width: 768px) {
  .aboutMainLeft {
    height: auto;
    float: left;
    width: 44%;
    margin-bottom: 50px;
    text-align: center;
    margin-left: 3%;
    margin-right: 3%;
  }

  .aboutMainLeft img {
    width: 100%;
    height: 100%;
    padding: 0;
    margin: 0;
    display: block;
  }

  .aboutMainLeftCard {
    margin: 0;
    padding: 0rem;
    height: 100%;
  }

  .aboutMainRight {
    float: right;
    width: 44%;
    margin-bottom: 50px;
    /* text-align: center; */
    margin-left: 3%;
    margin-right: 3%;
  }
}
